  &__title {
    font-weight: 700;
    font-size: 40px;
    line-height: 40px;
    letter-spacing: -1.5px;
    color: $white;

    @extend %displayflex;

    text-align: center;
    margin: auto;
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;

    height: 100vh;
    padding: $headerTP 0;
  }
}

.top-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
